import withErrorBoundary from '@app/ui/shared/ErrorBoundary';
import React, { ButtonHTMLAttributes } from 'react';
import styled, { StyledComponent } from 'styled-components';

export type ButtonProps = ButtonHTMLAttributes<any> & {
  variant?: 'default' | 'primary' | 'disabled',
  // disabled?: boolean,
  as?: string
} 

export const Button = styled.button<any>`
  padding: 15px 40px;
  border: none;
  border-radius: 6.25rem;
  cursor: pointer;
  letter-spacing: 0.8px;
  font-weight: 700;
  color: 'white';
  line-height: 1.1875rem;
  &:focus {
    outline: none;
  }
  &:hover {
    opacity: 0.8;
  }
  ${p => p.disabled && `
    cursor: auto;
    opacity: 0.5;
    pointer-events: none;
  `}
  ${p => p.theme.button[p.disabled && 'disabled' || p.variant || 'default']}
`

Button.defaultProps = {
  variant: 'default', 
} as Partial<ButtonProps>

export default withErrorBoundary(Button)
