import React from 'react';
import { Link } from 'gatsby';
import { connect } from 'react-redux';
import Layout from '@ui/layouts/Default';
import Image1 from '../components/Image1';
import SEO from '../components/seo';
import Todos from '../components/todos/todos';
import LoginSection from '../components/login/login-section';
import { IState } from '@app/store/createStore';
import { ITodo } from '@app/models/todo';
import ButtonLink from '@ui/components/ButtonLink';
import Button from '@ui/components/Button';
import Heading from '@ui/components/Heading/index';
import PageHeader from '../components/PageHeader';

const imgStyle = { maxWidth: '300px', marginBottom: '1.45rem' };

const pStyle = {
  fontSize: 'calc(5px + 3vw)',
  lineHeight: 'calc(12px + 3vw)',
  margin: '2px',
};

const IndexPage = ({ todos = [], userId = 0}: { todos: ITodo[] | undefined, userId: number | undefined }) => {

  return (
    <Layout>
      <SEO title='Home' />
      <PageHeader
        heading="Now go build something great!"
        subheading="Welcome to your new Gatsby site..."
      />

      {/* <div style={pStyle}>
        <p>Welcome to your new Gatsby site...</p>
        <p>Now go build something great!</p>
      </div> */}

      <Heading headingType='h1' headingText='Heading 1 - Page title' />
      <Heading headingType='eyebrow' headingText='Eyebrow Headline' />
      <Heading headingType='h2' headingText='Heading 2 - Section title' />
      <Heading headingType='h3' headingText='Heading 3 - Section title' />
      <Heading headingType='h4' headingText='Heading 4 - Small section title' />
      <Heading headingType='h5' headingText='Heading 5 - Card title' />
      <Heading headingType='h6' headingText='Heading 6 - Card title alternative' />
      <Heading headingType='h7' headingText='Heading 7 - Highlighted small title' />

      <Button>
        Default button
      </Button>
      <ButtonLink href="#">
        Button Link
      </ButtonLink>
      <ButtonLink href="#" disabled>
        Button Link Disabled
      </ButtonLink>

      <LoginSection userId={userId} />

      <Todos todos={todos} />

      <div style={imgStyle}>
        <Image1
          src={'https://picsum.photos/536/354'}
          caption="My caption"
        />
      </div>

      <Link to='/page-2/'>Go to page 2</Link>

    </Layout>
  );
};

const mapStateToProps = (state: IState) => {
  return {
    todos: state.todosReducer.todos,
    userId: state.loginReducer.userId,
  };
};

export default connect(mapStateToProps)(IndexPage);
